.CVHeader {
  color: black;
  display: flex;
  margin: auto;
  align-items: center;
  position: relative;
  background-color: #b5838d;
  width: 65%;
  flex-direction: row;
  padding-top: 3%;
}

.CVHeader-avatar-section {
  background-color: #b5838d;
  flex-direction: row;
  text-align: center;
  width: 55%;
}

.CVHeader-avatar {
  border-radius: 48%;
  width: 55%;
  background-color: #b5838d;
  animation: rotation 12s infinite linear;
  max-width: 100%;
  height: auto;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.CVHeader-fullname {
  font-size: 3.1vw;
  background-color: #b5838d;
  font-family: "Anton", sans-serif;
  flex-direction: row;
  text-align: center;
  width: 65%;
}

.CVHeader-profile-links {
  color: black;
  background-color: #b5838d;
  font-family: "Roboto", sans-serif;
  font-size: 1.1vw;
  flex-direction: row;
}

.CVHeader-profile-links a:link,
a:visited,
a:hover,
a:active {
  color: black;
}

li {
  background-color: #b5838d;
  color: #b5838d;
}

a {
  background-color: #b5838d;
}

.social-media-icon {
  padding-right: 3%;
  background-color: #b5838d;
}

